<!-- 订单 -->
<template>
	<view>
		<!-- 顶部导航栏 -->
		<view class="topNav" v-if="haveOrder">
			<view class="son" v-for="(k,i) in nav" :class="k.isAct?'act':''" @click="act(k)">
				{{k.name}}
			</view>
		</view>
		<!-- 无订单样式 -->
		<view class="noOrder" v-if="!haveOrder">
			<image :src="cdn+'/image/noList/noOrder.png'" mode="aspectFit"></image>
			<view class="text">暂无秒杀</view>
		</view>
		<!-- 内容 -->
		<view class="content" v-for="(k,i) in nav" v-if="haveOrder">
			<!-- {{k.name}} -->
			<view class="son" v-for="(k,i) in ordersMsg" :key="k.id">
				<view class="orderNum">订单号：{{k.orderNum}}</view>
				<view class="state" v-html="k.isPaid?'已付款':'待付款'" :style="{'color':k.isPaid?'':'#333333'}" ></view><!-- 订单状态 -->
				<view class="msg">
					<image :src="cdn+'/image/goods/common/goods_2.png'" mode="aspectFill"></image>
					<view class="name">{{k.name}}</view>
					<view class="price"><text style="font-size: 28rpx;font-weight: 400;">￥</text> {{k.price.toFixed(2)}}</view>
				</view>
				<view class="time">2019/11/06 13:46</view>
				<button v-if="!k.isPaid">去支付</button>
			</view>
		</view>
	</view>
</template>

<script>
	import {
		mapActions,
		mapState
	} from 'vuex'
	export default {
		data() {
			return {
				cdn: this.$conf.CDN_HOST,
				haveOrder: true, //是否有订单
				nav: [{ //导航栏
					name: '全部',
					isAct: true,
				}, {
					name: '待付款',
					isAct: false,
				}, {
					name: '已付款',
					isAct: false,
				}],
				//订单信息
				ordersMsg: [
					{
						orderNum: 24848157462144655,
						isPaid: true,
						pic: '',
						name: '专车空调全包班',
						price: 12000.00,
						time: '2019/11/06 13:46',
						id:1
					},{
						orderNum: 24848157462144655,
						isPaid: false,
						pic: '',
						name: '专车空调全包班',
						price: 12000.00,
						time: '2019/11/06 13:46',
						id:2
					}
				], 
			}
		},
		computed:{
			...mapState({
				orderList:state=>state.goods.orderMsg
			})
		},
		methods: {
			...mapActions({
				getOrderList_fun:'goods/getOrder'
			}),
			act(k) {
				for (var a = 0; a < this.nav.length; a++) {
					this.nav[a].isAct = false
				}
				k.isAct = true
			},
			async getOrderList(){
				let data={
					userId:this.$db.getSync('userId')
				}
				let res =await this.getOrderList_fun(data)
			}
		},
		onLoad() {
			if(this.ordersMsg.length==0)
			this.haveOrder=false
			else
			this.haveOrder=true
		}
	}
</script>

